export default class Graph{
  constructor(ctx,id,type,img,pos,edgeColor){
    this.ctx = ctx;
    this.id = id;
    this.type = type;
    this.img = img;
    this.pos = pos;
    this.edgeColor = edgeColor;
    this.angle = 0;
    this.width = 0;
    this.height = 0;
    this.isDraw = true
    this.isCurrent = false
  }
  //给选中的模型加标识
  chooseCurrent(bool){
    this.isCurrent = bool
  }
  // 在执行draw时是否执行画边框操作
  switchDraw(bool){
    this.isDraw = bool
  }
  //画图
  draw(){
    this.width = this.img.width;
    this.height = this.img.height;
    this.ctx.save();
    this.ctx.translate(this.pos.x,this.pos.y);
    this.ctx.rotate(this.angle);
    //在canvas里画图 设置图片的在x,y坐标里的参数
    this.ctx.drawImage(this.img,- this.width / 2, - this.height / 2);
    //绘制矩阵以便鼠标点击选中
    if(this.isDraw == true){
      this.ctx.strokeStyle = this.edgeColor;
      this.ctx.strokeRect(- this.width / 2 - 5 , - this.height / 2 - 5, this.width + 10 , this.height + 10);
    }
    this.ctx.restore();
  }
  //判断选中位置是否在canvas范围内
  inRange(x,y){
    let points = [
      [this.pos.x - this.width / 2, this.pos.y - this.height / 2],
      [this.pos.x + this.width / 2, this.pos.y - this.height / 2],
      [this.pos.x + this.width / 2, this.pos.y + this.height / 2],
      [this.pos.x - this.width / 2, this.pos.y + this.height / 2]
    ];
    let center = {
      x: this.pos.x,
      y: this.pos.y
    };
    for(let i = 0; i < points.length; i++){
      let x = points[i][0];
      let y = points[i][1];
      points[i][0] = (x - center.x) * Math.cos(this.angle) + (y - center.y) * Math.sin(this.angle) + center.x;
      points[i][1] = -(x - center.x) * Math.sin(this.angle) + (y - center.y) * Math.cos(this.angle) + center.y;
    }
    let inside = false;
    for (let i = 0, j = points.length - 1; i < points.length; j = i++) {
      let xi = points[i][0], yi = points[i][1];
      let xj = points[j][0], yj = points[j][1];
      let intersect = ((yi > y) !== (yj > y)) && (x < (xj - xi) * (y - yi) / (yj - yi) + xi);
      if (intersect) inside = !inside;
    }
    return inside;
  }
  //在canvas里更新新的坐标点位置
  move(x,y){
    this.pos.x = x;
    this.pos.y = y;
  }
  //给模型转45度角
  rotateSelf(){
    this.angle += Math.PI / 4;
  }
  //画边框
  drawEdges(){
    this.ctx.save();
    this.ctx.translate(this.pos.x,this.pos.y);
    this.ctx.rotate(this.angle);
    this.ctx.strokeStyle = 'red';
    this.ctx.strokeRect(- this.width / 2 - 5 , - this.height / 2 - 5, this.width + 10, this.height + 10);
    this.ctx.restore();
  }
  //清除当前模型
  clear(){
    this.ctx.save();
    this.ctx.translate(this.pos.x,this.pos.y);
    this.ctx.rotate(this.angle);
    this.ctx.clearRect(- this.width / 2 - 5 , - this.height / 2 - 5, this.width + 10, this.height + 10);
    this.ctx.restore();
  }
  //清除当前模型边框
  clearBorder(){
    this.ctx.save();
    this.ctx.translate(this.pos.x,this.pos.y);
    this.ctx.rotate(this.angle);
    this.ctx.lineWidth = 2;
    this.ctx.strokeStyle = "#ffffff";
    this.ctx.strokeRect(- this.width / 2 - 5 , - this.height / 2 - 5, this.width + 10, this.height + 10);
    this.ctx.restore();
  }
}
