<template>
  <div class="canvas">
    <SvgTools @sendTargetObj="sendTargetObj" @sendImageArr="sendImageArr" />
    <Canvas :targetId="targetId" :targetType="targetType" :imageArr="imageArr" :rubber="rubber"/>
    <Operate @sendIsClear="sendIsClear"/>
  </div>
</template>

<script>
import SvgTools from './../components/svg.vue'
import Canvas from './../components/canvas.vue'
import Operate from './../components/operate.vue'
export default {
    data () {
      return {
        targetId:0, 
        targetType:'',
        imageArr:[],
        rubber:{}
      }
    },
    methods:{
      //获得正在拖动模型的目标
      sendTargetObj(data){
        this.targetId = data.id
        this.targetType = data.type
      },
      //获得左侧模型类型为image的转图片格式的集合
      sendImageArr(data){
        this.imageArr = data
      },
      //获得是否可以涂擦
      sendIsClear({rubber}){
        this.rubber = rubber
      }
    },
    computed: {
      
    },
    components: {
      SvgTools,
      Canvas,
      Operate
    },
    created(){

    },
    mounted() {        
      this.$confirm('当使用橡皮擦功能后,如果新加模型进来会导致之前的canvas重置了,因为底层是记录每个模型画图方法,新添模型进来都会清空画布进行重绘  但如果你有解决每次清除模型不是通过clearRect进行覆盖的方法,那问题就变得简单了emmmm', '注意!!', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        
      }).catch(() => {
        
      })
    }
}
</script>

<style scoped>
  .canvas{
    display:flex;
  }
</style>
