<script>
export default {
    data () {
      return {
        
      }
    },
    methods:{
      //拖动
      drag(event){
        // 这里先清空所有的模型 对所有的模型进行重绘
        let canvas = this.$store.state.canvas.canvas
        let ctx = canvas.getContext("2d")
        ctx.clearRect(0,0,canvas.width,canvas.height)
        if(JSON.stringify(this.$store.state.canvas.obj) !== '{}'){
          for(let i = 0;i<this.$store.state.canvas.objList.length;i++){
            this.$store.state.canvas.objList[i].chooseCurrent(false)
            this.$store.state.canvas.objList[i].switchDraw(false)
            this.$store.state.canvas.objList[i].draw();
            this.$store.state.canvas.objList[i].switchDraw(true)
          }
        }
        //消除橡皮擦功能
        this.$store.state.canvas.isClear = false
        let ele = event.target;
        this.$emit('sendTargetObj',{id:parseInt(ele.dataset.id),type:ele.dataset.type})
      },
      //集成类型不同的数组模型集合 目前只集成类型为image的模型集合
      getTypeList(){
        return new Promise((resolve,reject) =>{
          let data = [] //储存类型为image的图片
          for(let i = 0;i<this.$refs.svg.children[0].children.length;i++){
            if(this.$refs.svg.children[0].children[i].children[0].dataset.type == 'icon'){

            }else if(this.$refs.svg.children[0].children[i].children[0].dataset.type == 'image'){
              data.push({image:this.$refs.svg.children[0].children[i].children[0].innerHTML,id:this.$refs.svg.children[0].children[i].children[0].dataset.id})
            }
          }
          resolve(data)
        })
      }
    },
    computed: {
      dataList(){
        return this.$store.state.canvas.dataList
      }
    },
    components: {
      
    },
    created(){
      
    },
    mounted() {
      //集成类型为image的模型集合 另外类型再看
      this.getTypeList().then(res=>{
        let DOMURL = window.URL || window.webkitURL || window;
        let imageArr = []  //image类型的模型集合
        for(let i = 0; i < res.length; i ++){
          imageArr[i] = new Image();
          let svg = new Blob([res[i].image], {type: 'image/svg+xml;charset=utf-8'});
          imageArr[i].id = res[i].id
          imageArr[i].src = DOMURL.createObjectURL(svg);
        }
        this.$emit('sendImageArr',imageArr)
      })
    }
}
</script>

