<template>
  <div class="canvas">
    <!--设置固定的长宽 如果设置响应式 可能会出现svg拖入canvas像素会变形-->
    <canvas ref="myCanvas"  width="500" height="300" style="border:1px solid #f1f1f1;" @mousedown="canvasDown($event)"  @mousemove="canvasMove($event)" @mouseup="canvasUp($event)" @dragover="dragOver($event)" @drop="dragFinished($event)"></canvas>
    <div class="footer">
      <button v-dbClick @click="outPng">生成png图片</button>
    </div>
  </div>
</template>

<script>
import Graph from './../utils/graph.js'
import html2canvas from 'html2canvas'
export default {
    props:{
      targetId:{
        type:Number,
        require:true
      },
      targetType:{
        type:String,
        require:true
      },
      imageArr:{
        type:Array,
        require:false
      },
      rubber:{
        type:Object,
        require:false
      }
    },
    data () {
      return {
        end: {
          x: 0,
          y: 0
        },
        mouseDown:false,  //判断是否按下拖动 否则不执行函数里方法
        isRevse:true  //判定一次反向数组
      }
    },
    methods:{
      canvasDown(event){
        console.log('canvasDown')
        //拦截因为橡皮擦导致的this.$store.state.canvas.objList结构混乱问题 所以橡皮擦处理过的canvas已经不能进行还原操作 只能重新做
        if(this.$store.state.canvas.objList.length == 0){
          return false
        }

        //判断点击坐标的是哪个模型对象
        let canvas = this.$store.state.canvas.canvas
        let ctx = canvas.getContext("2d")
        let address = this.canvasMousePos(canvas,event)  //获取鼠标点击坐标
        let objList
        if(this.isRevse == true){
          this.$store.state.canvas.objList = this.$store.state.canvas.objList.reverse()
          objList = this.$store.state.canvas.objList
          this.isRevse = false
        }else{
          objList = this.$store.state.canvas.objList
        }
        // 这里先清空所有的模型 对所有的模型进行重绘
        ctx.clearRect(0,0,canvas.width,canvas.height)
        for(let i = 0;i<objList.length;i++){
            objList[i].chooseCurrent(false)
            objList[i].switchDraw(false)
            objList[i].draw();
            this.$store.state.canvas.obj = {}
        }
        //目的是在选中图层时 如果存在图层在一起了 按照最上面的进行选中
        for(let i = 0;i<objList.length;i++){
          if(objList[i].pos.x-objList[i].width < address.x && address.x < (objList[i].pos.x + objList[i].width) && objList[i].pos.y-objList[i].height < address.y && address.y <(objList[i].pos.y + objList[i].height)){
            //选中模型画边框
            objList[i].chooseCurrent(true)
            objList[i].drawEdges()
            this.$store.state.canvas.obj = objList[i]
            //可以拖动了
            this.mouseDown = true
            break;
          }
        }
      },
      canvasMove(event){
        console.log('canvasMove')
        // 检测是否是用户点击了橡皮擦
        if(this.$store.state.canvas.isClear == true){
          let canvas = this.$store.state.canvas.canvas
          //清除上一帧动画
          this.rubber.clear()
          //实时获取当前选中模型的拖动位置坐标
          this.end = this.canvasMousePos(canvas,event);
          // 更新新的坐标点
          this.rubber.move(this.end.x,this.end.y);
          this.rubber.switchDraw(false)
          this.rubber.draw()
          // 相当于打乱了svg的结构 已经不能通过储存他们 进行重绘了
          this.$store.state.canvas.objList = []
          if(this.end.x < 30 || this.end.y < 30 || this.end.x > 470 || this.end.y > 270){
            this.rubber.clear()
          }
        }

        //如果mouseDown为true 则表示拖动 否则说明是点击选中
        if(this.mouseDown == true && this.$store.state.canvas.isClear == false){
          //没有想到很好的办法来处理拖动不会影响到其他的模型 因为本质用的都是用白色覆盖图层 
          // 所以目前做法是存储之前的模型及当前位置模型进行重绘

          //这里删除当前对象实时移动位置的前一帧 这里会出现如果同时有两个模型都需要在canvas理 
          // 那么就会出现两个id一样的模型 接着下面的for循环会把我不想删除的也删除了 所以我会在每个字段里再加个当前标识
          for(let i = 0;i<this.$store.state.canvas.objList.length;i++){
            if(this.$store.state.canvas.obj.id == this.$store.state.canvas.objList[i].id && this.$store.state.canvas.objList[i].isCurrent == true){
              this.$store.state.canvas.objList.splice(i,1)
              break;
            }
          }
          let canvas = this.$store.state.canvas.canvas
          let ctx = canvas.getContext("2d")
          //实时获取当前选中模型的拖动位置坐标
          this.end = this.canvasMousePos(canvas,event);
          // 更新新的坐标点
          this.$store.state.canvas.obj.move(this.end.x,this.end.y);
          //在这里对应之前删掉的补齐之前的 还是之前的模型 只不过是换了坐标点 其他信息不变
          this.$store.state.canvas.objList.push(this.$store.state.canvas.obj)
          // 这里先清空所有的模型 对所有的模型进行重绘
	        ctx.clearRect(0,0,canvas.width,canvas.height)
          for(let i = 0;i<this.$store.state.canvas.objList.length;i++){
            this.$store.state.canvas.objList[i].switchDraw(false)
            this.$store.state.canvas.objList[i].draw();
          }
          //再将新的坐标点进行重新绘制
          this.$store.state.canvas.obj.drawEdges();
        }
      },
      canvasUp(event){
        console.log('canvasUp')
        // 重置绘制效果
        for(let i = 0;i<this.$store.state.canvas.objList.length;i++){
          this.$store.state.canvas.objList[i].switchDraw(true)
        }
        this.mouseDown = false
      },
      dragOver(event){
        event.preventDefault();
      },
      //如果有滚动条 则获取滚动条与顶部的高度
      getScrollTop(){
        let scrollTop = 0;
        if (document.documentElement && document.documentElement.scrollTop) {
          scrollTop = document.documentElement.scrollTop;
        } else if (document.body) {
          scrollTop = document.body.scrollTop;
        }
        return scrollTop;
      },
      //获取投放在canvas标签里的横纵坐标点位置
      canvasMousePos(canvas, event){
        let x = (document.documentElement.scrollLeft || document.body.scrollLeft) + (event.clientX || event.pageX);
        let y = (event.clientY || event.pageY) - this.getScrollTop();
        return {
          x: x - canvas.offsetLeft,
          y: y - canvas.offsetTop
        }
      },
      //canvas拖拽结束 即放开鼠标按键 只会执行一次
      dragFinished(event){
        console.log('dragFinished')
        event.preventDefault();
        let canvas = this.$store.state.canvas.canvas
        let ctx = canvas.getContext("2d")
        this.end = this.canvasMousePos(canvas,event)  //获取到在canvas标签里放置的位置
        //这里开始区分模型类型 如Icon、graph等类型
        if(this.targetType == 'icon'){
          // this.$store.state.canvas.obj = new Icon(ctx,this.targetId,this.targetType,this.end,'red','white'); //获取到拖动模型的需要信息
        }else if(this.targetType == 'image'){
          //从所有类型为image的模型集合里找到我要的那个模型
          for(let i = 0;i<this.imageArr.length;i++){
            if(this.targetId == this.imageArr[i].id){
              this.$store.state.canvas.obj = new Graph(ctx,this.targetId,this.targetType,this.imageArr[i],this.end,'red');
              break;
            }
          }
        }
        //将模型放入objList集合中
        this.$store.state.canvas.objList.push(this.$store.state.canvas.obj)
        //开始绘制
        this.$store.state.canvas.obj.draw();
        //加标识
        this.$store.state.canvas.obj.chooseCurrent(true);
      },
      //生成图片
      outPng(){    
        var shareContent = this.$refs.myCanvas;//需要截图的包裹的（原生的）DOM 对象
        var width = shareContent.offsetWidth; //获取dom 宽度
        var height = shareContent.offsetHeight; //获取dom 高度
        var scale = 1; //定义任意放大倍数 支持小数
        var opts = {
            scale: scale, // 添加的scale 参数
            // logging: true, //日志开关，便于查看html2canvas的内部执行流程
            width: width, //dom 原始宽度
            height: height,
            useCORS: true // 【重要】开启跨域配置
        };

        var context = shareContent.getContext('2d');
        // 【重要】关闭抗锯齿
        context.mozImageSmoothingEnabled = false;
        context.webkitImageSmoothingEnabled = false;
        context.msImageSmoothingEnabled = false;
        context.imageSmoothingEnabled = false;

        html2canvas(shareContent,opts).then(canvas => {
            let url = canvas.toDataURL();
            let a = document.createElement('a');
            a.href = url;
            a.download = new Date() + ".png";
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
        })
      }
    },
    computed: {
      
    },
    components: {
      
    },
    created(){
      
    },
    mounted() {
      //储存全局canvas的dom对象
      this.$store.state.canvas.canvas = this.$refs.myCanvas 
    }
}
</script>

<style scoped>
  .canvas{
    /*flex:1;*/
    margin-left:15px;
    display:flex;
    flex-direction:column;
  }
  .footer{
    display:flex;
    margin-top:10px;
    height:35px;
    justify-content:center;
  }
  .footer button{
    border:1px solid #f1f1f1;
    background-color:#fff;
    border-radius:3px;
    font-size:12px;
  }
  .footer button:hover{
    color:#fff;
    background-color:#ace;
  }
</style>
