<template>
  <div class="operate">
    <ul v-if="JSON.stringify(isImage) == '{}'">
      <li @click="clearAll">全部清除</li>
      <div :style="isClear == false?{color:'#fff',backgroundColor:'#fff'}:{color:'#000',backgroundColor:'#ace'}" @click='drag($event)'>
        <svg version="1.1" xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 77 32">
          <title>rubber</title>
          <line x1="0" y1="30" x2="100" y2="30" style="stroke:#000;stroke-width:5;border-radius:3px;"/>
          <rect x="10" y="21" width="20" height="25" style="fill:#000;transform:rotate(-45deg)"/>
          <rect x="40" y="22" width="20" height="25" style="fill:#000;transform:rotate(-45deg)"/>
        </svg>
      </div>
    </ul>
    <ul v-else>
      <li @click="clearCurrent">当前清除</li>
      <li @click="rotateObj">旋转</li>
    </ul>
  </div>
</template>

<script>
import Graph from './../utils/graph.js'
export default {
    props:{
      
    },
    data () {
      return {
        
      }
    },
    methods:{
      //涂擦
      drag(event){
        let rubber = []
        let canvas = this.$store.state.canvas.canvas
        let ctx = canvas.getContext("2d")
        if(this.isClear == false){
          let img = `<svg version="1.1" xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 77 32"><title>rubber</title><line x1="0" y1="30" x2="100" y2="30" style="stroke:#000;stroke-width:5;border-radius:3px;"/><rect x="10" y="21" width="20" height="25" style="fill:#000;transform:rotate(-45deg)"/><rect x="40" y="22" width="20" height="25" style="fill:#000;transform:rotate(-45deg)"/></svg>`
          let DOMURL = window.URL || window.webkitURL || window;
          rubber[0] = new Image();
          let svg = new Blob([img], {type: 'image/svg+xml;charset=utf-8'});
          rubber[0].src = DOMURL.createObjectURL(svg);
        }
        this.$store.state.canvas.isClear = !this.$store.state.canvas.isClear
        this.$emit('sendIsClear',{rubber:new Graph(ctx,0,'image',rubber[0],{x:0,y:0},'red') || null})
      },
      //清除当前的模型对象
      clearCurrent(){
        //注意 需要去掉objList里的对象 否则鼠标点击还是会找寻 通过id去除
        for(let i = 0;i<this.$store.state.canvas.objList.length;i++){
          if(this.$store.state.canvas.obj.id == this.$store.state.canvas.objList[i].id && this.$store.state.canvas.objList[i].isCurrent == true){
            this.$store.state.canvas.objList.splice(i,1)
            break;
          }
        }
        // 这里先清空所有的模型 对所有的模型进行重绘
        let canvas = this.$store.state.canvas.canvas
        let ctx = canvas.getContext("2d")
        ctx.clearRect(0,0,canvas.width,canvas.height)
        if(JSON.stringify(this.$store.state.canvas.obj) !== '{}'){
          for(let i = 0;i<this.$store.state.canvas.objList.length;i++){
            this.$store.state.canvas.objList[i].switchDraw(false)
            this.$store.state.canvas.objList[i].draw();
            this.$store.state.canvas.objList[i].switchDraw(true)
          }
        }
        this.$store.state.canvas.obj = {}
      },
      //画布全部清除
      clearAll(){
        let canvas = this.$store.state.canvas.canvas
        let ctx = canvas.getContext("2d")
        //注意 需要去掉objList里的对象 否则鼠标点击还是会找寻
        this.$store.state.canvas.objList = []
	      ctx.clearRect(0,0,canvas.width,canvas.height)
      },
      //对当前选中模型进行旋转
      rotateObj(){
        //注意 需要去掉objList里的对象 否则鼠标点击还是会找寻 通过id去除
        for(let i = 0;i<this.$store.state.canvas.objList.length;i++){
          if(this.$store.state.canvas.obj.id == this.$store.state.canvas.objList[i].id && this.$store.state.canvas.objList[i].isCurrent == true){
            this.$store.state.canvas.objList.splice(i,1)
            break;
          }
        }
        //改变转向度数
        this.$store.state.canvas.obj.rotateSelf()
        this.$store.state.canvas.objList.push(this.$store.state.canvas.obj)
        // 这里先清空所有的模型 对所有的模型进行重绘
        if(JSON.stringify(this.$store.state.canvas.obj) !== '{}'){
          let canvas = this.$store.state.canvas.canvas
          let ctx = canvas.getContext("2d")
          ctx.clearRect(0,0,canvas.width,canvas.height)
          for(let i = 0;i<this.$store.state.canvas.objList.length;i++){
            if(this.$store.state.canvas.obj.id == this.$store.state.canvas.objList[i].id && this.$store.state.canvas.objList[i].isCurrent == true){
              this.$store.state.canvas.objList[i].chooseCurrent(true)
              this.$store.state.canvas.objList[i].switchDraw(true)
              this.$store.state.canvas.objList[i].draw();
            }else{
              this.$store.state.canvas.objList[i].switchDraw(false)
              this.$store.state.canvas.objList[i].draw();
              this.$store.state.canvas.objList[i].switchDraw(true)
              this.$store.state.canvas.objList[i].chooseCurrent(false)
            }
          }
        }
      }
    },
    computed: {
      isImage(){
        return this.$store.state.canvas.obj
      },
      isClear(){
        return this.$store.state.canvas.isClear
      }
    },
    components: {
      
    },
    created(){
      
    }
}
</script>

<style scoped>
  .operate ul{
    box-sizing:border-box;
    padding:0 15px;
  }
  .operate ul div{
    margin-bottom:15px;
    cursor:pointer;
    text-align:center;
    border:1px dashed #f1f1f1;
    border-radius:5px;
    padding-top: 10px;
  }
  .operate ul li{
    margin-bottom:15px;
    box-sizing:border-box;
    padding:5px 15px;
    border:1px dashed #f1f1f1;
    color:#ccc;
    cursor:pointer;
    text-align:center;
    border-radius:5px;
  }
  .operate ul li:hover{
    background-color:#ace;
    color:#fff;
  }
</style>
